.emptyCart {
  margin: auto;
  text-align: center;
  padding: 10vmax;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
}
.emptyCart > svg {
  font-size: 5vmax;
  color: rgb(0, 0, 0);
}
.emptyCart > p {
  font-size: 2vmax;
}
.emptyCart > a {
  background-color: rgb(51, 51, 51);
  color: white;
  border: none;
  padding: 1vmax 3vmax;
  cursor: pointer;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
  text-decoration: none;
}

.cartPage {
  padding: 6vmax;
}

.cartHeader {
  //   background-color: rgb(0, 0, 0);
  width: 90%;
  //   box-sizing: border-box;
  margin: auto;
  color: white;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
  //   font-weight: lighter;
  //   font-size: 3vh;
  text-align: left;
}
.cartHeader > p {
  margin: 10px;
  color: black;
}
.cartHeader > p:last-child {
  text-align: end;
}

.cartContainer {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
}

.cartInput {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  transform: translateX(12px);
  height: 8vmax;
}

.cartInput > button {
  border: none;
  background-color: rgba(0, 0, 0);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.cartInput > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.cartInput > input {
  border: none;
  padding: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
  color: rgba(0, 0, 0, 0.64);
}

.cartSubtotal {
  display: flex;
  padding: 0.5vmax;
  height: 8vmax;
  align-items: center;
  box-sizing: border-box;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
  justify-content: flex-end;
  color: rgba(0, 0, 0, 0.753);
}

.cartGrossProfit {
  display: grid;
  grid-template-columns: 2fr 1.2fr;
}

.cartGrossProfitBox {
  border-top: 3px solid rgb(0, 0, 0);
  margin: 1vmax 4vmax;
  box-sizing: border-box;
  padding: 2vmax 0;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
  display: flex;
  justify-content: space-between;
}
.cartGrossProfitBox > p {
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
}

.checkOutBtn {
  display: flex;
  justify-content: flex-end;
}
.checkOutBtn > button {
  background-color: rgb(0, 0, 0);
  color: white;
  border: none;
  padding: 0.8vmax 2vmax;
  width: 40%;
  font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";

  margin: 1vmax 4vmax;
  cursor: pointer;
  border-radius: 4px;
}

@media screen and (max-width: 600px) {
  .cartPage {
    padding: 0;
    min-height: 60vh;
  }

  .cartHeader {
    width: 100%;
    font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";

    grid-template-columns: 3fr 1fr 1fr;
  }

  .cartContainer {
    width: 100%;
    grid-template-columns: 3fr 1fr 1fr;
  }

  .cartInput {
    height: 20vmax;
  }

  .cartInput > button {
    padding: 1vmax;
  }

  .cartInput > input {
    width: 2vmax;
    padding: 1vmax;
    font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
    font: 300 1.7vmax;
  }

  .cartSubtotal {
    padding: 1.5vmax;
    height: 20vmax;
    font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
    // font: 300 1.7vmax;
  }

  .cartGrossProfit {
    display: grid;
    grid-template-columns: 0fr 2fr;
  }

  .cartGrossProfitBox {
    padding: 2vmax;
    font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
    font: 300 1.7vmax;
  }

  .checkOutBtn > button {
    padding: 2vmax 4vmax;
    width: 100%;
    font: 300 1.2vmax "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";

    font: 300 1.7vmax;
  }
}
